body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Base */

* {
    box-sizing: border-box;
    /* Making sure everything uses border-box sizing*/
}

body {
    background-color: #282c34;
    color: white;
}

/* .App-header {
    background-color: #282c34;
    min-height: 100vh;
    color: white;
} */

.App-Portfolios {
    display: block;
    min-width: 100vh;
}

#addPortfolioButton {
    padding: 20px 30px;
    margin: 20px;
}

button {
    background-color: darkolivegreen;
    border: none;
    color: white;
    padding: 5px 10px;
    margin: 5px;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
}

button:hover {
    -webkit-filter: opacity(0.7);
            filter: opacity(0.7);
}

.bold{
    font-weight: 800
}

.large{
    font-size: 24px;
}

.Info-Row{
    height: 80px;
}

.Error-Text{
    color: red;
}

.Disabled-Button{
    opacity: 0.4;
}

.Disabled-Button:hover{
    -webkit-filter: none;
            filter: none;
}

/*************
 * Portfolio *
 *************/

.App-Portfolio {
    background-color: whitesmoke;
    color: #282c34;
    margin: 20px;
    width: 800px;
    float: left;
}

.Portfolio-Title {
    font-weight: 1000;
    font-size: 18px;
}

/* Buttons */

.Portfolio-Board{
    background-color: lightgray;
}

.Portfolio-Board-Wrapper{
    height: 100px;
    overflow: auto;
}

.Portfolio-Board-Header-Wrapper{
    overflow: auto;
    width: 100%;
}

.Portfolio-Board-Scrollbar{
    width: 98%;
}

.large-col{
    width: 25%;
}

.small-col{
    width: 12.5%;
}

.Portfolio-Board th{
    background-color: darkgray
}

table {
    width: 100%;
    border: 1px solid lightgray;
  }
td {
    /* padding: 0px; */
    text-align: center;
    border-right: 1px solid darkgray;
    /* border-left: 1px solid darkgray; */
    border-bottom: 1px solid darkgray 
}


.Exit-Button {
    background-color: darkred;
    border-radius: 50%;
    float: right;
}

.row{
    width: 100%;
    padding: 10px;
}

.Add-Stock-Form{
    display: inline-block;
}

/*Text Input*/
.form-group{
    display: inline-block;
}
